he common styles for Angular Material. We include this here so that you only\r\n// have to load a single css file for Angular Material in your app.\r\n// **Be sure that you only ever include this mixin once!**\r\n@include mat-core();\r\n\r\n// Define the default theme (same as the example above).\r\n$candy-app-primary: mat-palette($mat-indigo);\r\n$candy-app-accent: mat-palette($mat-pink, A200, A100, A400);\r\n$candy-app-warn: mat-palette($mat-red, A200, A100, A400);\r\n$candy-app-theme: mat-light-theme($candy-app-primary, $candy-app-accent);\r\n\r\n\r\n// Define an alternate dark theme.\r\n$dark-primary: mat-palette($mat-pink);\r\n$dark-accent: mat-palette($mat-blue-grey, A200, A100, A400);\r\n$dark-warn: mat-palette($mat-deep-orange);\r\n$dark-theme: mat-dark-theme($dark-primary, $dark-accent, $dark-warn);\r\n\r\n// Include the alternative theme styles inside of a block with a CSS class. You can make this\r\n// CSS class whatever you want. In this example, any component inside of an element with\r\n// `.dark-theme` will be affected by this alternate dark theme instead of the default theme.","// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // T